.link {
  @include transition(color);
  color: #fff;
  outline: none;

  &:hover,
  &:focus {
    color: $accent-color;
  }

  &--footer-bottom-row {
    &:hover,
    &:focus {
      color: $accent-color;
    }
  }

  &--footer-top-row {
    padding-left: 44px;
    color: #000;
  }
}
